@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin graphic-retina($filename, $extname: "jpg") {
  background-image: url("../img/#{$filename}.#{$extname}");

  @include retina {
    background-image: url("../img/#{$filename}@2x.#{$extname}");
  }
}

@mixin graphic-adaptive($filename, $extname: "jpg") {
  @media (min-width: $vp-1024) {
    @include graphic-retina("#{$filename}-desktop", $extname);
  }

  @media (min-width: $vp-768) {
    @media (max-width: ($vp-1024 - 1)) {
      @include graphic-retina("#{$filename}-tablet", $extname);
    }
  }

  @include vp-767 {
    @include graphic-retina("#{$filename}-mobile", $extname);
  }
}

@mixin hover {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }
}

@mixin focus {
  &.focus-visible:focus {
    @content;
  }
}

@mixin active {
  &.focus-visible:active {
    @content;
  }
}

@mixin hover-focus {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }

  &.focus-visible:focus {
    @content;
  }
}

// Desktop first

@mixin vp-1919 {
  @media (max-width: ($vp-1920 - 1px)) {
    @content;
  }
}

@mixin vp-1439 {
  @media (max-width: ($vp-1440 - 1px)) {
    @content;
  }
}

@mixin vp-1279 {
  @media (max-width: ($vp-1280 - 1px)) {
    @content;
  }
}

@mixin vp-1023 {
  @media (max-width: ($vp-1024 - 1px)) {
    @content;
  }
}

@mixin vp-767 {
  @media (max-width: ($vp-768 - 1px)) {
    @content;
  }
}

@mixin vp-374 {
  @media (max-width: ($vp-375 - 1px)) {
    @content;
  }
}

