.about {
  padding-bottom: 100px;

  @include vp-1023 {
    padding-bottom: 83px;
  }

  @include vp-767 {
    padding-bottom: 79px;
  }
}

.about__container {
  display: grid;
  grid-template-columns: 150px 1fr;
  gap: 100px;

  @include vp-1023 {
    gap: 25px;
  }

  @include vp-767 {
    display: block;
  }
}

.about__image {
  @include vp-767 {
    display: none;
  }
}

.about__content {
  h2 {
    margin: 0;
    margin-bottom: 14px;
    padding: 0;
    font-family: $ff-Roboto-Condensed;
    font-weight: 400;
    font-size: 36px;
    line-height: 51px;

    @include vp-1023 {
      margin-bottom: 5px;
    }

    @include vp-767 {
      margin-bottom: 0;
      font-size: 28px;
      line-height: 39px;
    }
  }

  p {
    margin: 0;
    margin-bottom: 16px;
    padding: 0;
    line-height: 22px;
    overflow: hidden;

    @include vp-1023 {
      margin-top: 9px;
      margin-bottom: 0;
      padding-bottom: 10px;
    }

    @include vp-767 {
      margin-top: 11px;
      margin-bottom: 0;
      padding-bottom: 6px;
      font-size: 14px;
      line-height: 19px;
    }
  }
}

.about__content.is-active {
    [data-about-accordion="mobile"] {
      display: inline;
    }
  }

[data-about-accordion="mobile"] {
  display: inline;

  @include vp-767 {
    display: none;
  }
}

.about__button {
  min-width: 180px;
  margin-top: 25px;
  padding: 14px 50px;
  color: $color-deep-sky-blue;
  font-size: 14px;
  line-height: 16px;
  border: 1px solid $color-deep-sky-blue;

  @include hover-focus {
    background-color: $color-deep-sky-blue;
    color: $color-alice-blue;
  }

  @include vp-1023 {
    padding-bottom: 13px;
    margin-top: 7px;
  }

  @include vp-767 {
    margin-top: 10px;
    min-width: 160px;
    padding: 8px 40px 10px;
    font-size: 14px;
    line-height: 16px;
  }
}
