  .header {
    position: absolute;
  }
}

.header__container {
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
  padding-bottom: 35px;
  padding-right: 5.2%;
  align-items: flex-start;
}
@media (max-width: 1023px) {
  .header__container {
    padding-top: 10px;
    padding-bottom: 33px;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding-right: 35px;
    padding-bottom: 11px;
  }
}

.header__logo {
