}
@media (max-width: 1023px) {
  .header__logo {
    padding-top: 10px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    width: 55px;
    padding-right: 20px;
    padding-top: 15px;
  }
}

.header__contacts {
  display: grid;
