.catalog li::before {
  background: rgba(4, 24, 39, 0.5);
  content: "";
  height: 100%;
  position: absolute;
  top: 0;
  transition: 0.3s background-color;
  width: 100%;
}
.catalog li::after {
  background-color: #00b2ff;
  background-image: url("./img/svg/card-arrow.svg");
  background-position: calc(50% + 3px);
  background-repeat: no-repeat;
  background-size: 6px;
  border-radius: 3px 0 0 0;
  bottom: 0;
  content: "";
  height: 60px;
  position: absolute;
  right: 0;
  width: 60px;
}
@media (max-width: 767px) {
  .catalog li::after {
    background-position: calc(50% + 2px);
    background-size: 4px;
    height: 40px;
    width: 40px;
  }
}
